@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600&display=swap');

.navbar-lateral{
  background: #222c3c;
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 250px;
}
.navbar-lateral hr{
  border: 0;
  background-color: #b7c0cd;
  height: 1px;
  margin: 0;
}
.navbar-lateral h4{
  margin: 25px 0px 15px;
  padding-left: 30px ;
  color: #7f8fa4;
  font-size: 12px;
}
.content{
  box-shadow: 0px 0px 2px gray;
  height: 100%;
  width: (100%-250px);
  margin-left: 250px;
}
.navbar-superior{
  height: 60px;
}
.sidebar-list{
  list-style: none;
  font-size: 0.9em;
  color: #b7c0cd;
  /* line-height: 1.5em; */
  margin: 0px 0px 0px 0px;
  padding: 0;
}
.sidebar-item{
  /* background: #1d2531; */
  padding: 0px 30px 0px 30px;
  height: 40px;
  display: flex;
  align-items: center;
  /* justify-content: center; */
}
.sidebar-item i{
  width: 17px;
  margin-right: 10px;
}
.sidebar-item:hover{
  color: white;
  background: #1d2531;
  cursor: pointer;
}
.title-home{
  text-decoration: none;
  font-size: 1.2em;
  color: white;
}
.title-home:hover{
  color: white;
}
.name-title{
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.sub-contenido{
  background: rgba(93, 106, 180, 0.05);
  min-height: 100vh;
  height: 100%;
  padding: 20px 50px 50px 50px;
}

/* Estilo input number */
input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
  
}
input[type=number] {
  -moz-appearance: textfield;
}
.input-estilo:focus{
  outline: none !important;
  border: 1px solid #c1d7fb;
}
.trak ul{
  list-style: none;
}
.como-funciona-top{
  height: 40%;
  background: #F8D615;
  width: 100%;
}
.como-funciona-bot{
  height: 60%;
  background: #F9FAFF;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
  color: #082A5A;
  display: flex;
  align-items: center;
  justify-content: center;
}
.como-funciona-top p{
  color: #082A5A;
  font-weight: 400;
}
.como-funciona-top h5{
  color: #082A5A;
  font-weight: 500;
}
.como-funciona-top h1{
  font-weight: 800;
  color: #082A5A;
}
.como-funciona-bot p{
  font-family: 'Montserrat', sans-serif;
  color: #0a2c58;
  font-weight: 400;
}
.como-funciona-bot h4{
  color: #082A5A;
  font-weight: 800;
}
.llegamos-top{
  height: 60%;
  background: #F8D615;
  width: 100%;
}
.llegamos-top h1{
  color: #082A5A;
  font-weight: 800;
  font-family: 'Montserrat', sans-serif;
}
.llegamos-top ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
.llegamos-top li{
  color: #082A5A;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  margin: 12px 0px 12px 0px;
}
.llegamos-bot{
  height: 40%;
  background: #F9FAFF;
  width: 100%;
  color: #082A5A;
  font-family: 'Montserrat', sans-serif;
}
.llegamos-bot h1{
  font-weight: 800;
}
.llegamos-bot p{
  font-weight: 400;
}
.footer{
  background: #082A5A;
  height: 300px;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
}
.footer h1{
  color: #F8D615;
  font-weight: 800;
}
.footer p{
  color: white;
  font-weight: 400;
}
.footer h5{
  color: #F8D615;
  font-weight: 800;
}
.footer span{
  font-size: 0.6em;
}
.quienes-somos-1{
  color: #082A5A;
  font-family: 'Montserrat', sans-serif;
  height: 600px;
}
.quienes-somos-1 h1{
  font-weight: 800;
}
.quienes-somos-1 p{
  font-weight: 400;
}
.quienes-somos-2{
  background: #F8D615;
  font-family: 'Montserrat',sans-serif;
  color: #082A5A;
  height: 450px;
}
.quienes-somos-2 h1{
  font-weight: 800;
}
.quienes-somos-2 p{
  font-weight: 400;
}
.quienes-somos-2 h4{
  font-weight: 800;
}
.quienes-somos-3{
  height: 450px;
  background: #F9FAFF;
  color: #082A5A;
  font-family: 'Montserrat', sans-serif;
}
.quienes-somos-3 h1{
  font-weight: 800;
}
.quienes-somos-3 p{
  font-weight: 600;
}
.quienes-somos-3 input{
  outline: none !important;
  border: 2px solid #082A5A;
}
.quienes-somos-3 input:focus{
  border: 2px solid #082A5A;
}
.quienes-somos-3 input::placeholder{
  color: #082A5A;
  font-weight: 600;
  font-family: 'Montserrat', sans-serif;
}
.quienes-somos-3 textarea{
  outline: none !important;
  border: 2px solid #082A5A;
}
.quienes-somos-3 textarea:focus{
  border: 2px solid #082A5A;
}
.quienes-somos-3 textarea::placeholder{
  color: #082A5A;
  font-weight: 600;
  font-family: 'Montserrat', sans-serif;
}
.logeo-usuarios{
  font-family: 'Montserrat',sans-serif;
  color: #082A5A;
}
.logeo-usuarios input{
  outline: none !important;
  border: 2px solid #082A5A;
}
.logeo-usuarios input:focus{
  border: 2px solid #082A5A;
}
.logeo-usuarios input::placeholder{
  color: #082A5A;
  font-weight: 600;
  font-family: 'Montserrat', sans-serif;
}
.logeo-usuarios h4{
  font-weight: 600;
}

.appp {
  text-align: center;
}

.appp-header {
  background-color: #F9FAFF;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  /* color: white; */
}

@media (max-width: 500px) {
  .nav-computer{
    display: none;
  }
}
@media (min-width: 500px) {
  .nav-mobile{
    display: none;
  }
}